import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent} from './home/home.component';
import { DiaryComponent} from './diary/diary.component';
import {AboutComponent} from './about/about.component';
import {DiaryJanuaryComponent} from './diary-january/diary-january.component';
import {DiaryFebruaryComponent} from './diary-february/diary-february.component';
import {DiaryMarchComponent} from './diary-march/diary-march.component';
import {DiaryAprilComponent} from './diary-april/diary-april.component';
import {DiaryMayComponent} from './diary-may/diary-may.component';
import {DiaryJuneComponent} from './diary-june/diary-june.component';
import {DiaryJanuaryTwoComponent} from './diary-january-two/diary-january-two.component';

const routes: Routes = [
  {path: '' , component : HomeComponent},
  {path: 'diary' , component : DiaryComponent},
  {path: 'about' , component : AboutComponent},
  {path: 'diary-january' , component : DiaryJanuaryComponent},
  {path: 'diary-january-two' , component : DiaryJanuaryTwoComponent},
  {path: 'diary-february' , component : DiaryFebruaryComponent},
  {path: 'diary-march' , component : DiaryMarchComponent},
  {path: 'diary-april' , component : DiaryAprilComponent},
  {path: 'diary-may' , component : DiaryMayComponent},
  {path: 'diary-june' , component : DiaryJuneComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
